
.login-form{
    max-width: 100%;
    width: 500px;
    border: 1px solid rgba(238, 238, 238, 0.3);
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    padding: 20px 10px;
    border-radius: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -90%);
            transform: translate(-50%, -90%);
    padding-top: 40px;
}
.login-form .form{
    margin: 0
}
.login-form span.lock{
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    top: 0;
    left: 0;
    -webkit-transform: translate(20%, -50%);
            transform: translate(20%, -50%);
    background: #2e3141;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    border: 1px solid rgba(238, 238, 238, 0.3)
}
.login-form span.lock i{
    margin: auto;
    color: #fff;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-size: 1.5em
}
.login-form input{
    margin-bottom: 15px;
}



